$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
    $addSize: $pcSize - $mobSize;
    $addMobSize: $addSize + $addSize * 0.7;
    @media (max-width: 768px) {
        font-size: calc(#{$mobSize + px} + #{$addMobSize} * ((100vw - 320px) / #{$maxWidth}));
    }
    @media (min-width: 768px) {
        font-size: calc(#{$mobSize + px} + #{$addSize} * (100vw / #{$maxWidth}));
    }
}

.slider-main__title {
    @include adaptiv-font(48, 20);
}

.slider-main__more {
    @include adaptiv-font(16, 14);
}

section .title {
    @include adaptiv-font(40, 20);
}


.store__section .title {
    @include adaptiv-font(56, 35);
}

.about__bg .title {
    @include adaptiv-font(65, 40);
}

.coworking__bg .title {
    @include adaptiv-font(65, 40);
}


.about__item-title {
    @include adaptiv-font(40, 30);
}

.article-in__item-title {
    @include adaptiv-font(22, 18);
}

.text {
    @include adaptiv-font(18, 16);
}

.contact__form .title {
    @include adaptiv-font(24, 20);
}

